.moviesList {
    display: grid;
    padding: 5px;
    grid: repeat(4, 350px) / repeat(4, 290px);
    grid-gap: 5px;
    grid-auto-flow: column;
    width: calc(100% - 4px);
    overflow-x: scroll;
    overflow-y: hidden;
  }
  
  .moviesItem {
    position: relative;
    overflow: hidden;
  }
  
  .moviesItem:hover img {
    transform: rotate(360deg);
    /* transform: rotateY(45geg); */
  
    /* cursor: zoom-in; */
  }
  
  .titleBox {
    content: '';
    height: 100px;
    position: absolute;
    left: 0;
    bottom: 0;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
    width: 100%;
    z-index: 1;
  }
  
  .poster {
    object-fit: cover;
    object-position: top;
    height: 100%;
    width: 100%;
    transition: 0.3s ease;
  }
  
  .title {
    position: absolute;
    width: 100%;
    margin: 0;
    bottom: 0;
    color: #fff;
    padding: 10px;
    z-index: 10;
    cursor: default;
  }
  
  .title:nth-child(1) {
    grid-column: span 2;
    grid-row: span 2;
    font-size: 20px;
  }