.SearchForm {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 400px;
    background-color: #ececec;
    border-radius: 3px;
    overflow: hidden;
    margin-right: auto;
    margin-left: auto;
  }
  
  .SearchFormButton {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg');
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
  }
  
  .SearchFormButton:hover {
    opacity: 1;
  }
  
  .SearchFormButtonLabel {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0;
  }
  
  .SearchFormInput {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
    background-color: #ececec;
  }
  
  .SearchFormInput::placeholder {
    font: inherit;
    font-size: 18px;
  }